
.sample__container{padding:10px;border:1px solid rgba(255,255,255,0.2);border-radius:3px;margin:0 0 20px
}
.sample__container:last-child{margin:0
}
.sample__tabs-body{padding:10px;background:#fff;border-radius:0 0 3px 3px
}
.slide-fade-enter-active{transition:all 0.3s ease
}
.slide-fade-leave-active{transition:all 0.1s cubic-bezier(1, 0.5, 0.8, 1)
}
.slide-fade-enter,.slide-fade-leave-active{padding-left:10px;opacity:0
}



